import Vue from 'vue';
import Router from 'vue-router';
import HomePage from './components/Home.vue';
import PlayerPage from './components/Player.vue';
import LoginPage from './components/Login.vue';
import PaymentPage from './components/Payment.vue';
import RegisterPage from './components/Register.vue';
import PlayerHPage from './components/PlayerH.vue';
import NewPlayer from './components/NewPlayer.vue';
import MePage from './components/Me.vue';
import { getToken,setTouristId,getInvitationcode } from "@/utils";
import { recordUserBehavior } from "@/api/h5api";
Vue.use(Router);


// 定义路由时必须要定义meta中的action字段，
const router = new Router({
	routes: [
		{ path: '/', component: HomePage ,meta:{action:"跳转至首页"}},
		{ path: '/player', name: 'Player', component: PlayerPage, meta: { requiresAuth: true,action:"跳转至播放页" } },
		{ path: '/playerh', name: 'PlayerH', component: PlayerHPage,meta:{action:"跳转至播放页"} },
		{ path: '/newPlayer', name: 'newPlayer', component: NewPlayer,meta:{action:"跳转至播放页"} },
		{ path: '/me', name: 'Me', component: MePage, meta: { requiresAuth: true ,action:"跳转至个人中心"}  },
		{ path: '/login', component: LoginPage ,meta:{action:"跳转至登录"} },
		{ path: '/register', component: RegisterPage ,meta:{action:"跳转至注册"}  },
		{ path: '/payment', component: PaymentPage, meta: { requiresAuth: true,action:"跳转至注册"} }
	]
});

// 添加全局导航守卫
router.beforeEach((to, from, next) => {
	const isAuthenticated = getToken();
    const invitationcode = getInvitationcode();
	// 判断是否有用户信息，没有则生成游客id
	setTouristId();
	recordUserBehavior(to.fullPath,invitationcode,to.meta.action);
	if (to.matched.some(record => record.meta.requiresAuth)) {
		// 需要认证的路由
		if (!isAuthenticated ) {
			next({
				path: '/login',
				query: { redirect: to.fullPath }  // 保存目标路由路径
			});
		} else {
			next();
		}
	} else {
		// 不需要认证的路由
		next();
	}
});

export default router; 