<template>
  <div class="me-container" dir="rtl">
    <!-- 仿照抖音个人简介，调整返回按钮位置 -->
    <div class="back-button-container" style="position: relative; top: -20px; left: 10px;">
      <van-icon name="arrow-left" class="back-icon" size="24px" @click="goBack" />
    </div>

    <!-- 用户信息头部，仿照抖音添加关注和粉丝数 -->
    <div class="user-header">
      <div class="user-basic" style="flex-direction: column; align-items: center; justify-content: center;">
        <!-- <div class="avatar" style="width: 120px; height: 120px; font-size: 48px;">
          {{ userAvatar }}
        </div> -->
        <div class="user-info" style="margin: 20px 0 0 0;">
          <h2 style="font-size: 24px;"><van-icon name="user" size="30"/> {{ userInfo.username }}</h2>
          <!-- 账户币种 -->
          <div class="user-details" style="justify-content: center;">
            <span>{{ userInfo.currency }}</span>
          </div>
          <!-- 金币余额 -->
          <div class="user-details" style="justify-content: center;">
            <span>رصيد الذهب:{{ userCoin }} <img :src="require('@/assets/img/金币.png')" width="15px"/></span>
            <button class="recharge-button custom-payment-button" @click="showRechargepop">تعبئة الرصيد</button>
          </div>
        </div>
      </div>
    </div>

    <!-- 播放记录、收藏记录、留言记录和支付记录 -->
    <div class="tab-container" style="display: flex; justify-content: space-around; border-bottom: 1px solid #eee;">
      <div class="tab-item" style="padding: 15px 0;width: 25%; cursor: pointer;"
        :class="{ active: activeTab === 'play' }" @click="changeTab('play')">سجل التشغيل</div>
      <div class="tab-item" style="padding: 15px 0;width: 25%; cursor: pointer;"
        :class="{ active: activeTab === 'collect' }" @click="changeTab('collect')">سجل الإعجابات</div>
      <div class="tab-item" style="padding: 15px 0;width: 25%; cursor: pointer;"
        :class="{ active: activeTab === 'message' }" @click="changeTab('message')">سجل الرسائل</div>
      <div class="tab-item" style="padding: 15px 0;width: 25%; cursor: pointer;"
        :class="{ active: activeTab === 'payment' }" @click="changeTab('payment')">سجل الدفع</div>
    </div>
    <!-- 播放记录内容 -->
    <div v-if="activeTab === 'play'" class="play-history" ref="playHistoryRef" >
      <div class="play-cards-container" @scroll="handlePlayHistoryScroll" style="width: 100%; height: 670px; overflow-y: auto; background-color: rgb(248, 249, 250);">
        <div v-if="playHistory.length === 0 && !loadingPlayHistory" class="no-data">
          لا يوجد أيّ سجل حتى الآن
        </div>
        <div class="movie-grid">
        <div v-for="(item, index) in playHistory" :key="index"  class="movie-item" @click="goToPlayerH(item.videoId,item.id)">
          <div class="movie-card">
            <!-- <div class="corner-badge"  v-if="item.videonew == '1'">
              <img 
                :src="require('@/assets/img/subscript/upNew.png')"
                alt="上新"
              />
            </div>
            <div class="corner-badge"  v-else-if="item.videofree == '1'">
              <img 
                :src="require('@/assets/img/subscript/free.png')"
                alt="免费"
              />
            </div>
            <div class="corner-badge"  v-else-if="item.videofree == '2'">
              <img 
                :src="require('@/assets/img/subscript/realisticDiscounts.png')"
                alt="限时免费"
              />
            </div>
            <div class="corner-badge"  v-else-if="item.videodiscount == '1'">
              <img 
                :src="require('@/assets/img/subscript/discount.png')"
                alt="优惠"
              />
            </div> -->
            <img :src=item.cover alt="" class="movie-cover" />
            <div class="play-button">
              <van-icon name="play-circle-o" size="24" />
            </div>
          </div>
          <div class="movie-title">{{ item.title }}</div>
        </div>
      </div>
        <div class="play-cards">
          <div v-if="loadingPlayHistory" class="loading-more">
            جاري التحميل...
          </div>
        </div>
      </div>
    </div>
    <!-- 收藏记录内容 -->
    <div v-if="activeTab === 'collect'" class="play-history">
      <div class="play-cards-container" @scroll="handleCollectHistoryScroll" style="width: 100%; height: 670px; overflow-y: auto; background-color: rgb(248, 249, 250);">
        <div v-if="collectHistory.length === 0 && !loadingCollectHistory" class="no-data">
          لا يوجد أيّ سجل حتى الآن
        </div>
        <div class="movie-grid">
        <div v-for="(item, index) in collectHistory" :key="index"  class="movie-item" @click="goToPlayerH(item.videoId,item.id)">
          <div class="movie-card">
            <img :src=item.cover alt="" class="movie-cover" />
            <div class="play-button">
              <van-icon name="play-circle-o" size="24" />
            </div>
          </div>
          <div class="movie-title">{{ item.title }}</div>
        </div>
      </div>
        <div class="play-cards">
          <div v-if="loadingCollectHistory" class="loading-more">
            جاري التحميل...
          </div>
        </div>
      </div>
    </div>
    <!-- 留言记录内容 -->
    <div v-if="activeTab === 'message'" class="order-history" style="background-color: #f5f5f5;">
      <div class="play-cards-container" @scroll="handleMessageHistoryScroll" style="width: 100%; height: 670px; overflow-y: auto; background-color: rgb(248, 249, 250);">
        <div v-if="messageHistory.length === 0 && !loadingMessages" class="no-data"  style="color: #6c757d;">
          لا يوجد أيّ سجل حتى الآن
        </div>
        <div class="order-cards">
          <div v-for="(item, index) in messageHistory" :key="index" class="order-cards" style="border-bottom: 1px solid #e0e0e0; background-color: #f8f9fa;">
            <div class="order-info" style="display: flex; flex-direction: column;">
              <div style="display: flex; padding:0 25px; justify-content: space-between;">
                <span style="font-size: 20px; display: block; text-align: right; padding:0 0 10px 25px; color: #343a40;">{{ item.title }}</span>
                <span style="font-size: 16px; display: block; text-align: right; padding:0 0 10px 25px;">
                  <span v-if="item.state === 0" style="color: #28a745">تم الإرسال</span>
                  <span v-else-if="item.state === 1" style="color: #007bff">تم الرد</span>
                  <span v-else style="color: #dc3545">حالة غير معروفة</span>
                </span>
              </div>
              <div style="display: flex; padding:0 25px; justify-content: space-between;">
                <span style="color: #6c757d;">{{ formatTime(item.time) }}</span>
              </div>
            </div>
          </div>
          <div v-if="loadingMessages" class="loading-more" style="color: #007bff;">
            جاري التحميل...
          </div>
        </div>
      </div>
    </div>
    <!-- 支付记录内容 -->
    <div v-if="activeTab === 'payment'" class="order-history" style="background-color: #f5f5f5;">
      <div class="play-cards-container" @scroll="handlePaymentScroll" style="width: 100%;height: 670px; overflow-y: auto; background-color:rgb(248, 249, 250);">
        <div v-if="paymentHistory.length === 0 && !loadingOrders" class="no-data" style="color: #6c757d;">
          لا توجد سجلات الدفع
        </div>
        <div class="order-cards">
          <div v-for="(item, index) in paymentHistory" :key="index" class="order-cards" style="border-bottom: 1px solid #e0e0e0; background-color: #f8f9fa;">
            <div class="order-info" style="display: flex; flex-direction: column;">
              <span style="font-size: 20px; display: block; text-align: right; padding:0 25px 10px 25px; color: #343a40;">{{ item.title }}</span>
              <div style="display: flex; padding:0 25px; justify-content: space-between;">
                <span style="color: #28a745;">{{ item.payCurrency }}  {{ item.payprice }}</span>
                <span style="color: #6c757d;">{{ formatTime(item.time) }}</span>
              </div>
            </div>
          </div>
          <div v-if="loadingOrders" class="loading-more" style="color: #007bff;">
            جاري التحميل...
          </div>
        </div>
      </div>
    </div>

    <!-- 金币充值 -->
    <van-popup
      v-model="showCoinPayMentPopup"
      position="bottom"
      :style="{ height: '62%', maxHeight: '62%' }"
      @open="setCoinPayMentDefaultSelection"
    >
      <div class="payment-popup-content">
        <!-- 支付信息 -->
        <h3 class="payment-title">معلومات الشحن</h3>
        <p class="payment-description">يرجى شحن منصة الذهب</p>
        <h4 class="vip-title">شحن العتاد</h4>
        <div class="vip-container">
          <div
            v-for="coin in coinPriceInfo?.coins"
            :key="coin.code"
            class="vip-card"
            :class="{ 'card-selected': selectedCoinOption?.code == coin.code }"
            @click="selectCoinOption(coin)"
          >
            <span class="vip-card-title">عملة ذهبية {{ coin.coin }}</span>
            <span class="vip-card-title">هدية   {{ coin.present }} </span>
            <span class="price-container">
              <span class="price">{{ coin.price }}</span>
              <span class="unit">USD</span>
            </span>
          </div>
        </div>
        <h4 class="payment-methods-title">اختر طريقة الدفع</h4>
        <div class="payment-methods">
          <div class="payment-methods-container">
            <div
              class="payment-method-card"
              :class="{ selected: selectedCoinPaymentMethod.id === method.id }"
              @click="selectPaymentMethod(method)"
              v-for="method in coinPaymentMethods"
              :key="method.id"
            >
              <img
                :src="method.img"
                alt="支付方式图标"
                class="payment-method-icon"
              />
            </div>
          </div>
        </div>
        <div class="payment-summary">
          <!-- 待付金额 -->
          <p>
            المبلغ المستحق للدفع:
            <span class="total-amount">{{ coinTotalAmount?.toFixed(2) }}</span>
            USD
          </p>
          <button
            class="payment-button custom-payment-button"
            @click="handlePayment"
          >
            <!-- 立即支付 -->
            الدفع الآن
          </button>
        </div>
      </div>
      <form name="payssion_payment" ref="payssionForm" action="https://www.payssion.com/payment/create.html" method="post" style="display: none;">
        <input type="hidden" id="api_key" name="api_key" value="">
        <input type="hidden" id="api_sig" name="api_sig" value="">
        <input type="hidden" id="pm_id" name="pm_id" value="">
        <input type="hidden" id="order_id" name="order_id" value="">
        <input type="hidden" id="description" name="description" value="">
        <input type="hidden" id="amount" name="amount" value="">
        <input type="hidden" id="currency" name="currency" value="">
        <input type="hidden" id="return_url" name="return_url" value="">
        <input type ="submit" id="pay" value ="Submit">
      </form>
    </van-popup>

    <!-- 添加退出登录按钮，仿照抖音调整位置 -->
    <!-- <div class="logout-container" style="text-align: center; margin: 30px 0;">
      <van-button class="logout-button" block @click="handleLogout"
        >تسجيل خروج</van-button
      >
    </div> -->
  </div>
</template>

<script>
import { Icon,Toast } from 'vant';
import { views, orders, queryBalanceByuserId,
  pay, 
  messages, collects,getCoinPrice,makePayment,
  coinOrder ,getUser
} from "../api/api";
import { getUserInfo } from "../utils/index";
import {
  syncOrder,getPaySign
} from "../api/h5api";
export default {
  name: "UserProfile",
  components: {
    'van-icon': Icon
  },
  data() {
    return {
      balance: 0,
      playHistory: [],
      collectHistory: [],
      messageHistory: [],
      paymentHistory: [],
      playHistoryPage: 1,
      collectHistoryPage: 1,
      messageHistoryPage: 1,
      orderPage: 1,
      selectedCurrency : 'USD', 
      loadingPlayHistory: false,
      loadingOrders: false,
      loadingCollectHistory: false,
      loadingMessages: false,
      hasMorePlays: true,
      hasMoreOrders: true,
      hasMoreCollects: true,
      hasMoreMessages: true,
      showCoinPayMentPopup: false,
      coinInfo: {},
      selectedCoinOption:{},
      coinPaymentMethods: [],
      selectedCoinPaymentMethod: {},
      coinTotalAmount: 0,
      coinPriceInfo: {},
      userCoin:0,
      activeTab: 'play' // 新增，用于控制当前激活的标签页
    };
  },
  mounted() {
    this.loadPlayHistory();//页面打开默认为播放记录
    this.loadOrders();
    this.loadCollectHistory();
    // this.loadMessageHistory();
    this.queryBalance(); // 调用查询余额的方法，初始加载时调用一次。
    this.refreshUserCoin();
  },
  computed: {
    currency() {
      return getUserInfo()?.currency || "USD";
    },
    userInfo() {
      return getUserInfo();
    },
    userAvatar() {
      return this.userInfo.mail
        ? this.userInfo.mail.charAt(0).toUpperCase()
        : "?";
    },
  },
  created() {
    this.getCoinPayMentInfo();
    this.fetchPayDetails();
  },
  methods: {
    showRechargepop(){
      this.showCoinPayMentPopup = true;
    },
    async refreshUserCoin(){
     const respone = await getUser();
     const balance = respone.data.balance;
     this.userCoin = balance;
    },
    selectPaymentMethod(method) {
      this.selectedCoinPaymentMethod = method;
    },
    async handlePayment() {
      this.showPaymentPopup = false;
      Toast.loading({
        message: "支付中...",
        forbidClick: true,
      });
      console.log("支付信息:", this.selectedCoinOption.code);
      //预生成金币充值订单
      const signalOrderResponse = await coinOrder(this.selectedCoinOption.code,null);
      console.log("信号订单结果:", signalOrderResponse);
      // 如果当前用户选择的是Payssion支付的话则拉起Payssion支付，否则仍使用Payment支付
      if(this.selectedCoinPaymentMethod.id == 'mada_sa' || this.selectedCoinPaymentMethod.id =='stcpay_sa' || this.selectedCoinPaymentMethod.id =='applepay_sa'){
        //同步订单给网站后台
        syncOrder(signalOrderResponse.data.oId,getUserInfo()?.userId || '',this.coinTotalAmount.toFixed(2),'USD','payssion','pending','coin',this.selectedCoinPaymentMethod.id);
        // 触发表单提交
        if (this.$refs.payssionForm) {
          //支付方式
          const pmIdInput = this.$refs.payssionForm?.querySelector('#pm_id');
          if (pmIdInput) {
            pmIdInput.value = this.selectedCoinPaymentMethod.id;
          }
          //金额
          const amountInput = this.$refs.payssionForm?.querySelector('#amount');
          if (amountInput) {
            amountInput.value = this.coinTotalAmount.toFixed(2);
          }
          //币种
          const currencyInput = this.$refs.payssionForm?.querySelector('#currency');
          if (currencyInput) {
            currencyInput.value = 'USD';
          }
          //订单号
          const orderIdInput = this.$refs.payssionForm?.querySelector('#order_id');
          if (orderIdInput) {
            orderIdInput.value = signalOrderResponse.data.oId;
          }
          //描述
          const descriptionInput = this.$refs.payssionForm?.querySelector('#description');
          if (descriptionInput) {
            descriptionInput.value = signalOrderResponse.data.oId +'الدفع للطلب ';
          }
          //返回地址
          const returnUrlInput = this.$refs.payssionForm?.querySelector('#return_url');
          if (returnUrlInput) {
            returnUrlInput.value = window.location.href;
          }
          //api_key
          const apiKeyInput = this.$refs.payssionForm?.querySelector('#api_key');
          if (apiKeyInput) {
            const api_key = process.env.VUE_APP_PAY_API_KEY;
            apiKeyInput.value = api_key;
          }
          //api_sig
          const apiSigInput = this.$refs.payssionForm?.querySelector('#api_sig');
          if (apiSigInput) {
            const api_sig =  await getPaySign(signalOrderResponse.data.oId,this.coinTotalAmount.toFixed(2),'USD',this.selectedCoinPaymentMethod.id);
            console.log("api_sig",api_sig);
            console.log("api_sig",api_sig.data);
            apiSigInput.value = api_sig.data;
          }
          this.$refs.payssionForm.submit();
        }
      }else{
        // 触发Payment支付
        const payResponse = await pay(
          signalOrderResponse.data.oId,
          this.selectedCoinPaymentMethod.id,
          this.coinTotalAmount.toFixed(2)
        );
        console.log("payResponse", payResponse);
        window.location.href = payResponse;
      }
    },
    selectCoinOption(option){
      this.selectedCoinOption = {
        code: option.code,
        price: option.price,
        coin: option.coin,
        present: option.present,
      };
      this.coinTotalAmount = Number(option.price);
    },
    async getCoinPayMentInfo() {
      const priceResponse = await getCoinPrice();
          this.coinPriceInfo = {
            coins: priceResponse.data,
          };
    },
    fetchPayDetails() {
      this.getPayDetail(); 
    },
    async getPayDetail() {
      try {
        const makePaymentResponse = await makePayment();
        const payssionMethods = [
          {
            "img": require('@/assets/img/sa_pay/mada_sa.png'),
            "id": "mada_sa"
          },
          {
              "img": require('@/assets/img/sa_pay/stcpay_sa.png'),

              "id": "stcpay_sa"
          },
          {
              "img": require('@/assets/img/sa_pay/applepay_sa.png'),
              "id": "applepay_sa"
          }];
        this.coinPaymentMethods = makePaymentResponse.data;
        const payCurrency = getUserInfo()?.currency || 'USD';
        if( payCurrency == 'SAR'){
          this.coinPaymentMethods = payssionMethods;
        }
        this.selectedCoinPaymentMethod = this.coinPaymentMethods[0];
      } catch (error) {
        console.error("获取影片详情失败:", error);
      }
    },
    setCoinPayMentDefaultSelection(){
      this.selectCoinOption(this.coinPriceInfo.coins[0]);
    },
    async loadPlayHistory() {
      if (!this.hasMorePlays || this.loadingPlayHistory) return;

      this.loadingPlayHistory = true;
      try {
        const response = await views(this.playHistoryPage, 10);

        if (response.data && response.data.length) {
          this.playHistory = [...this.playHistory, ...response.data];
          if(response.pageNo == response.totalPage){
            this.hasMorePlays = false;
          }else{
            this.playHistoryPage++;
          }
        } else {
          this.hasMorePlays = false;
        }
      } catch (error) {
        console.error("加载播放记录失败:", error);
      } finally {
        this.loadingPlayHistory = false;
      }
    },
    async loadCollectHistory() {
      if (!this.hasMoreCollects || this.loadingCollectHistory) return;

      this.loadingCollectHistory = true;
      try {
        const response = await collects(this.collectHistoryPage, 10);

        if (response.data && response.data.length) {
          this.collectHistory = [...this.collectHistory, ...response.data];
          if(response.pageNo == response.totalPage){
            this.hasMoreCollects = false;
          }else{
            this.collectHistoryPage++;
          }
        } else {
          this.hasMoreCollects = false;
        }
      } catch (error) {
        console.error("加载收藏记录失败:", error);
      } finally {
        this.loadingCollectHistory = false;
      }
    },
    async loadMessageHistory() {
      if (!this.hasMoreMessages || this.loadingMessages) return;

      this.loadingMessages = true;
      try {
        const response = await messages(this.messageHistoryPage, 10);

        if (response.data && response.data.length) {
          this.messageHistory = [...this.messageHistory, ...response.data];
          this.messageHistoryPage++;
        } else {
          this.hasMoreMessages = false;
        }
      } catch (error) {
        console.error("加载留言记录失败:", error);
      } finally {
        this.loadingMessages = false;
      }
    },
    async loadOrders() {
      if (!this.hasMoreOrders || this.loadingOrders) return;

      this.loadingOrders = true;
      try {
        const response = await orders(this.orderPage, 10);

        if (response.data && response.data.length) {
          this.paymentHistory = [...this.paymentHistory, ...response.data];
          this.orderPage++;
        } else {
          this.hasMoreOrders = false;
        }
      } catch (error) {
        console.error("加载订单失败:", error);
      } finally {
        this.loadingOrders = false;
      }
    },
    goToPlayerH(movieId,episodeId) {
      this.$router.push({ name: "PlayerH", query: { id: movieId ,episodeId:episodeId} });
    },
    queryBalance() {
      const userInfo = this.userInfo.userId; // 获取用户信息
      const response = queryBalanceByuserId(userInfo);
      console.log(response);
      this.balance = userInfo;
    },
    handlePlayHistoryScroll(e) {
      const container = e.target;
      // RTL布局下，scrollLeft为负值，需要取绝对值
      const scrollLeft = Math.abs(container.scrollLeft);
      const scrollWidth = container.scrollWidth;
      const clientWidth = container.clientWidth;

      // 当滚动到距离左边界小于100px时加载更多
      if (scrollWidth - (scrollLeft + clientWidth) < 100) {
        this.loadPlayHistory();
      }
    },
    handleCollectHistoryScroll(e) {
      const container = e.target;
      // RTL布局下，scrollLeft为负值，需要取绝对值
      const scrollLeft = Math.abs(container.scrollLeft);
      const scrollWidth = container.scrollWidth;
      const clientWidth = container.clientWidth;

      // 当滚动到距离左边界小于100px时加载更多
      if (scrollWidth - (scrollLeft + clientWidth) < 100) {
        this.loadCollectHistory();
      }
    },
    handleMessageHistoryScroll(e) {
      const container = e.target;
      // RTL布局下，scrollLeft为负值，需要取绝对值
      const scrollLeft = Math.abs(container.scrollLeft);
      const scrollWidth = container.scrollWidth;
      const clientWidth = container.clientWidth;

      // 当滚动到距离左边界小于100px时加载更多
      if (scrollWidth - (scrollLeft + clientWidth) < 100) {
        this.loadMessageHistory();
      }
    },
    handlePaymentScroll(e) {
      const container = e.target;
      const scrollLeft = Math.abs(container.scrollLeft);
      const scrollWidth = container.scrollWidth;
      const clientWidth = container.clientWidth;

      if (scrollWidth - (scrollLeft + clientWidth) < 100) {
        this.loadOrders();
      }
    },

    getPaymentType(type) {
      const types = {
        2: "شراء الحلقة",
        3: "شراء العضوية",
        // 根据实际类型添加更多映射
      };
      return types[type] || "آخر";
    },
    goBack() {
      this.$router.go(-1); // 返回到上一个页面
    },
    async handleLogout() {
      try {
        // 清除本地存储的用户信息
        localStorage.removeItem("userInfo");
        localStorage.removeItem("token");

        // 跳转到登录页面
        this.$router.replace("/login");
      } catch (error) {
        console.error("退出登录失败:", error);
      }
    },
    changeTab(tab) {
      this.activeTab = tab;
    },
    formatTime(timestamp) {
      const date = new Date(timestamp);
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      return `${year}-${month}-${day} ${hours}:${minutes}`;
    },
  },
};
</script>

