export const getToken = () => {
	return localStorage.getItem("token");
};

export const setToken = (token) => {
	localStorage.setItem("token", token);
};

export const setInvitationcode = (token) => {
	localStorage.setItem("thatsbookInvitationcode", token);
};

export const getInvitationcode = () => {
	return localStorage.getItem("thatsbookInvitationcode");
};

export const removeToken = () => {
	localStorage.removeItem("token");
};

export const setUserInfo = (userInfo) => {
	localStorage.setItem("userInfo", JSON.stringify(userInfo));
};

export const getUserInfo = () => {
	return JSON.parse(localStorage.getItem("userInfo"));
};

export const setTouristId = () => {
	if (!getUserInfo() && !getTouristId()) {
		const touristId = '游客' + generateRandomUserId();
		console.log("游客id", touristId);
		localStorage.setItem("touristId", touristId);
	}
};

export const getTouristId = () => {
	return localStorage.getItem("touristId");
};

const generateRandomUserId = () => {
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    let userId = '';
    for (let i = 0; i < 9; i++) {
        const randomIndex = Math.floor(Math.random() * characters.length);
        userId += characters.charAt(randomIndex);
    }
    return userId;
};