<template>
  <div class="player-container" dir="rtl"
    style="width: 100vw; height: 100vh; position: fixed; top: 0; left: 0; overflow: hidden">
    <van-icon name="arrow-left" class="back-icon" size="6vw" color=#fff @click="goBack" />
    <div class="video-container" style="width: 100vw; height: 98vh; position: relative; transition: transform 0.2s;"
      @touchstart="handleVerticalTouchStart" @touchmove="handleVerticalTouchMove" @touchend="handleVerticalTouchEnd"
      @click="showVideoState">
      <video ref="tcplayerContainer" id="tcplayerContainer" class="tcplayer" style="width: 100%; height: 100%;"
        playsinline webkit-playsinline x5-playsinline :poster="this.selectedThumbnail" @timeupdate="updateProgress"></video>
      <!-- 剧集信息 -->
      <div class="episode-index-info" dir="ltr" style="opacity: 0.7;">
        <p style="font-size:5vw;">{{ this.currentEpisode.title }}</p>
      </div>
      <!-- 暂停图标 -->
      <div class="episode-pause-info" :class="{ 'fade-out': !isPause }">
        <img :src="require('@/assets/img/play.png')" style="opacity:0.7;width:17vw;" />
      </div>
      <!-- 下滑图标 -->
      <!-- <div class="slide-info" :class="{ 'slide-info-show': slideDirection == 'top'}"> -->
      <div class="slide-info">
        <img :src="require('@/assets/img/up-slide.png')" />
        <p>下滑播放上集</p>
      </div>
      <!-- 下滑停止图标 -->
      <!-- <div class="slide-info" :class="{ 'slide-info-show': slideDirection == 'top-stop'}"> -->
      <div class="slide-info">
        <img :src="require('@/assets/img/up-slide.png')" />
        <p>松开手指播放</p>
      </div>
      <!-- 上滑图标 -->
      <!-- <div class="slide-info" :class="{ 'slide-info-show': slideDirection == 'down'}"> -->
      <div class="slide-info">
        <img :src="require('@/assets/img/down-slide.png')" />
        <p>上滑播放下集</p>
      </div>
      <!-- 上滑停止图标 -->
      <!-- <div class="slide-info" :class="{ 'slide-info-show': slideDirection == 'down-stop'}"> -->
      <div class="slide-info">
        <img :src="require('@/assets/img/down-slide.png')" />
        <p>松开手指播放</p>
      </div>
      <!-- 全屏图标 -->
      <div class="episode-fullScreen" id="episode-fullScreen" :class="{ 'hidden': !isLandscapeVideo }"
        style="display: flex;align-items: center;width:auto;flex-direction: row-reverse;">
        <div style="width:100%">التبديل إلى وضع ملء الشاشة</div>
        <img :src="require('@/assets/img/fullScreen.png')" style="opacity:0.7;width:20px;" />
      </div>
      <!-- 自定义换集按钮 -->
      <div class="change-episode-bar" id="change-episode-bar" dir="ltr">
        <button class="change-episode-button"><img id="previous-episode"
            :src="require('@/assets/img/previous-episode.png')" style="width: 2.7vh;" /></button>
        <button class="change-episode-button"><img id="next-episode" :src="require('@/assets/img/next-episode.png')"
            style="width: 2.7vh;" /></button>
      </div>
      <!-- 右侧控制按钮隐藏建 -->
      <div class="hidden-bar" id="hidden-bar" @click="hideActionBar">
        <div class="hidden-button">
          <img :src="require('@/assets/img/hidden.png')" style="margin:auto;width:2.7vh;"/>
          <span style="margin:5px 0 0 0; font-weight: bold;font-size: 1.4vh;">{{ hideInfo }}</span>
        </div>
      </div>
      <!-- 自定义进度条 -->
      <div class="progress-bar" id="progress-bar" :class="{ 'progress-bar-seeking': isSeeking }" @click="seekVideo"
        @touchstart="handleProgressTouchStart" @touchmove="handleProgressTouchMove" @touchend="handleProgressTouchEnd"
        dir="ltr">
        <div class="progress" :style="{ width: progress + '%' }" dir="ltr"></div>
      </div>
      <!-- 左下角剧集信息 -->
      <div class="episode-info" id="episode-info" :class="{ 'hidden': isEpisodeInfoHidden }">
        <h4 v-if="this.title">{{ this.title }}</h4>
        <h4 v-else>تحميل . . . . ـ</h4>
        <p v-if="this.desc" style="font-size: 14px;">{{ truncatedDesc }}</p>
        <p v-else style="font-size: 14px;">مقدمة موجزة</p>
      </div>
      <!-- 右侧控制按钮 -->
      <div class="acton-button" :class="{'fade-out':isHiddenActionBar}" >
        <!-- 点赞按钮 -->
        <div class="like-button" :class="{ 'fullscreen': isFullScreen }">
          <van-icon name="like" :color="isLiked ? '#ff4d4f' : '#fff'" size="2.7vh" />
          <span style="margin-top: 5px; font-weight: bold;font-size:1.4vh;">إعجاب</span>
        </div>
        <!-- 调查问卷 -->
        <div class="like-button" :class="{ 'fullscreen': isFullScreen }" @click="questionnaire">
          <van-icon name="records" :color="'#fff'" size="2.7vh" />
          <span style="margin-top: 5px; font-weight: bold;font-size:1.4vh;">استبيان</span>
        </div>
        <!-- APP下载 -->
        <div class="like-button" :class="{ 'fullscreen': isFullScreen }">
          <img :src="require('@/assets/img/download.png')" style="width:2.7vh;"/>
          <span style="margin-top: 5px; font-weight: bold;font-size:1.4vh;">تنزيل التطبيق</span>
        </div>
        <!-- 转发网址 -->
        <div class="like-button" :class="{ 'fullscreen': isFullScreen }">
          <van-icon name="share" :color="'#fff'" size="2.7vh" />
          <span style="margin-top: 5px; font-weight: bold;font-size:1.4vh;">إعادة النشر</span>
        </div>
        <!-- 简介按钮 -->
        <div class="like-button" :class="{ 'fullscreen': isFullScreen }">
          <van-icon name="column" :color="'#fff'" size="2.7vh" />
          <span style="margin-top: 5px; font-weight: bold;font-size:1.4vh;">الملخص</span>
        </div>
        <!-- 选集 -->
        <div class="like-button" :class="{ 'fullscreen': isFullScreen }" @click="episodeSelection">
          <img :src="require('@/assets/img/selectEsipode.png')" style="width:2.7vh;"/>
          <span style="margin-top: 5px; font-weight: bold;font-size:1.4vh;">اختيار الحلقة</span>
        </div>
      </div>
    </div>

    <!-- 调查问卷 -->
    <van-popup v-model="showQuestionnaire" position="bottom" :style="{ height: 'auto', maxHeight: '55%' }"
      @open="setDefaultQuestionnaire" @close="setPlayerPlay">
      <div class="survey-popup-content" style="display: flex; flex-direction: column; height: 100%;">
        <p class="payment-title" style="font-size: 20px;padding-right:4%;">استبيان استطلاع</p>
        <!-- 根据数据量遍历生成问卷 -->
        <div v-for="(question, questionIndex) in surveyQuestions" :key="question.id" style="padding-right:4%;">
          <p class="payment-title">{{ question.content }}</p>
          <div v-if="question.questionType == 1">
            <!-- questionType为1时是单选框 -->
            <van-radio-group v-model="radioAnswers[questionIndex]">
              <van-radio v-for="(option, index) in question.options" :key="index" :name="option"
                style="margin-top: 10px;" @click="clickQuertstion(question.id, option)">
                &nbsp;{{ option }}
              </van-radio>
            </van-radio-group>
            <!-- questionType为3时是输入框 -->
          </div>
          <div v-if="question.questionType == 2">
            <!-- questionType为2时是多选框 -->
            <van-checkbox-group v-model="checkBoxOptions">
              <van-checkbox v-for="(option, index) in question.options" :key="index" :name="option"
                style="margin-top: 10px;" @click="clickCheckBoxOptions(question.id)" shape="square">
                &nbsp;{{ option }}
              </van-checkbox>
            </van-checkbox-group>
          </div>
        </div>
        <div style="padding-top:10px;position: sticky; bottom: 0; z-index: 1;">
          <!-- 提交 -->
          <button class="payment-button custom-payment-button" @click="submitUserQuestionnaire">إرسال</button>
        </div>
      </div>
    </van-popup>

    <!-- 支付 -->
    <van-popup v-model="showPaymentPopup" position="bottom" :style="{ height: '80%', maxHeight: '80%' }" lock-scroll
      @open="setDefaultSelection" @close="onCancelPay">
      <div class="payment-popup-content" style="height:99%;max-height:99%">
        <!-- 单集购买、设置自动、支付信息 -->
        <div
          style="display: flex;flex-direction: row;justify-content: space-between;position: sticky; top: 0; background-color: white; z-index: 1;">
          <div style="width:33%">
            <p class="payment-title" style="margin: 5px 0;">السعر: {{ priceInfo?.data?.price }} عملة</p>
            <p class="payment-description" style="margin: 5px 0;">لرصيد: {{ coinBalance }} عملة</p>
          </div>
          <div style="width: 67%;display: flex;flex-direction: row;justify-content: flex-end;">
            <van-checkbox v-model="autoBuy" @click="clickCheckBox">الخصم التلقائي للعملات</van-checkbox>
            <button class="buy-one-button custom-payment-button" @click="buyOneEpisode" style="margin-left: 7px;">شراء
              الحلقة</button>
          </div>
        </div>
        <!-- 整集购买 -->
        <div class="vip-container">
          <!-- 买全集（始终显示） -->
          <div class="vip-card single-episode-card" :class="{
            'card-selected': selectedOption?.payType === 'all',
            'card-disabled': !priceInfo?.data?.videoPrice // 新增禁用样式类
          }" :disabled="!priceInfo?.data?.videoPrice" @click="
            priceInfo?.data?.videoPrice && selectOption({
              price: priceInfo?.data?.videoPrice,
              payType: 'all',
              selectCurrency: currency
            })
            ">
            <span>شراء فائض</span>
            <span class="price-container">
              <span class="price" style="font-size: 20px!important;">{{
                (priceInfo?.data?.videoPrice)?.toFixed(
                  2
                ) || "-"
              }}</span>
              <span class="unit" style="font-size: 20px!important;">{{ currency }}</span>
            </span>
          </div>
        </div>
        <!-- VIP购买 -->
        <h4 class="vip-title">شراء عضوية VIP</h4>
        <div class="vip-container">
          <div class="vip-card" :class="{ 'card-selected': selectedOption?.id === vip.id }" @click="selectOption({
            id: vip.id,
            price: vip.price,
            payType: 'vip',
            selectCurrency: 'USD'
          })" v-for="vip in priceInfo?.vips" :key="vip.id">
            <span class="vip-card-title">{{ vip.info }}</span>
            <span class="price-container">
              <span class="price">{{ vip.price }}</span><span class="unit">USD</span>
            </span>
            <div class="unit" style="text-decoration: line-through; color: rgba(0, 0, 0, 0.5)">
              {{ vip.oldPrice }}
            </div>
          </div>
        </div>
        <!-- 金币充值 -->
        <h4 class="vip-title">عملة شحن</h4>
        <div class="vip-container">
          <div v-for="coin in coinPriceInfo?.coins" :key="coin.code" class="vip-card"
            :class="{ 'card-selected': selectedOption?.code === coin.code }" @click="selectOption({
              id: coin.code,
              code: coin.code,
              price: coin.price,
              payType: 'coin',
              selectCurrency: 'USD'
            })">
            <span class="vip-card-title">عملة ذهبية {{ coin.coin }}</span>
            <span class="vip-card-title">هدية {{ coin.present }} </span>
            <span class="price-container">
              <span class="price">{{ coin.price }}</span>
              <span class="unit">USD</span>
            </span>
          </div>
        </div>
        <!-- 付款方式 -->
        <div style="padding-top:10px;position: sticky; bottom: 0; background-color: white; z-index: 1;">
          <h4 class="payment-methods-title" style="margin:0">اختر طريقة الدفع</h4>
          <div class="payment-methods" style="margin-top:5px;">
            <div class="payment-methods-container">
              <div class="payment-method-card" :class="{ selected: selectedPaymentMethod.id === method.id }"
                @click="selectPaymentMethod(method)" v-for="method in paymentMethods" :key="method.id">
                <img :src="method.img" alt="支付方式图标" class="payment-method-icon"
                  :style="{ width: method.id === 'coin' ? '45px' : '', height: method.id === 'coin' ? '45px' : '' }" />
              </div>
            </div>
          </div>
          <div class="payment-summary">
            <!-- 待付金额 -->
            <p>
              المبلغ المستحق للدفع:
              <span class="total-amount">{{ totalAmount?.toFixed(2) }}</span>
              {{ this.selectedCurrency }}
            </p>
            <button class="payment-button custom-payment-button" @click="handlePayment">
              <!-- 立即支付 -->
              الدفع الآن
            </button>
          </div>
        </div>

      </div>
      <form name="payssion_payment" ref="payssionForm" action="https://www.payssion.com/payment/create.html"
        method="post" style="display: none;">
        <input type="hidden" id="api_key" name="api_key" readonly value="">
        <input type="hidden" id="api_sig" name="api_sig" readonly value="">
        <input type="hidden" id="pm_id" name="pm_id" readonly value="">
        <input type="hidden" id="order_id" name="order_id" readonly value="">
        <input type="hidden" id="description" name="description" readonly value="">
        <input type="hidden" id="amount" name="amount" readonly value="">
        <input type="hidden" id="currency" name="currency" readonly value="">
        <input type="hidden" id="return_url" name="return_url" readonly value="">
        <input type="submit" id="pay" value="Submit">
      </form>
    </van-popup>

    <!-- 取消支付的提示信息 -->
    <van-popup v-model="showCancelPay" position="center" round
      style="width:270px;height:440px;overflow:hidden;background:transparent" @click="onCancelPayClick">
      <div style="position: relative;width: 100%;">
        <img :src="require('@/assets/img/discount.png')" alt="推荐图片" class="recommend-image"
          style="width:100%;height:100%;" />
      </div>
      <van-icon name="close" id="closeAdIcon" @click="onCancelPayClick" size="24px" color=#fff
        style="position: absolute; bottom: 10px; left: 50%; transform: translateX(-50%); z-index: 1;" />
    </van-popup>

    <!-- 取消支付后的弹出整剧支付 -->
    <van-popup v-model="showAllEpisodePopup" position="bottom" :style="{ height: 'auto', maxHeight: '80%' }" lock-scroll
      @open="setAllEpisodeDefaultSelection">
      <div class="payment-popup-content" style="margin-top: 10px;">
        <!-- 整集购买 -->
        <div class="vip-container">
          <!-- 买全集（始终显示） -->
          <div class="vip-card single-episode-card" :class="{
            'card-selected': selectedOption?.payType === 'all',
            'card-disabled': !priceInfo?.data?.videoPrice // 新增禁用样式类
          }" :disabled="!priceInfo?.data?.videoPrice" @click="
            priceInfo?.data?.videoPrice && selectOption({
              price: priceInfo?.data?.videoPrice,
              payType: 'all',
            })
            ">
            <span>شراء فائض</span>
            <span class="price-container">
              <span class="price" style="font-size: 20px!important;">{{
                (priceInfo?.data?.videoPrice)?.toFixed(
                  2
                ) || "-"
              }}</span>
              <span class="unit" style="font-size: 20px!important;">{{ currency }}</span>
            </span>
          </div>
        </div>
        <!-- 付款方式 -->
        <div style="padding-top:10px;position: sticky; bottom: 0; background-color: white; z-index: 1;">
          <h4 class="payment-methods-title">اختر طريقة الدفع</h4>
          <div class="payment-methods">
            <div class="payment-methods-container">
              <div class="payment-method-card" :class="{ selected: selectedPaymentMethod.id === method.id }"
                @click="selectPaymentMethod(method)" v-for="method in paymentMethods" :key="method.id">
                <img :src="method.img" alt="支付方式图标" class="payment-method-icon"
                  :style="{ width: method.id === 'coin' ? '45px' : '', height: method.id === 'coin' ? '45px' : '' }" />
              </div>
            </div>
          </div>
          <div class="payment-summary">
            <!-- 待付金额 -->
            <p>
              المبلغ المستحق للدفع:
              <span class="total-amount">{{ totalAmount?.toFixed(2) }}</span>
              {{ currency }}
            </p>
            <button class="payment-button custom-payment-button" @click="handlePayment">
              <!-- 立即支付 -->
              الدفع الآن
            </button>
          </div>
        </div>
      </div>
      <form name="payssion_payment" ref="payssionForm" action="https://www.payssion.com/payment/create.html"
        method="post" style="display: none;">
        <input type="hidden" id="api_key" name="api_key" readonly value="">
        <input type="hidden" id="api_sig" name="api_sig" readonly value="">
        <input type="hidden" id="pm_id" name="pm_id" readonly value="">
        <input type="hidden" id="order_id" name="order_id" readonly value="">
        <input type="hidden" id="description" name="description" readonly value="">
        <input type="hidden" id="amount" name="amount" readonly value="">
        <input type="hidden" id="currency" name="currency" readonly value="">
        <input type="hidden" id="return_url" name="return_url" readonly value="">
        <input type="submit" id="pay" value="Submit">
      </form>
    </van-popup>

    <!-- 选集 -->
    <van-popup v-model="showEpisodeSelection" position="bottom" :style="{ height: '70%', maxHeight: '70%' }">
      <div class="popup-header">
        <div class="popup-info">
          <h3>{{ title }}</h3>
          <p>عدد الحلقات: {{ episodes.length }}</p>
        </div>
        <img :src="selectedThumbnail" alt="صورة مصغرة" class="thumbnail" />
      </div>
      <van-grid :column-num="4" :gutter="10" style="padding: 0 10px">
        <van-grid-item v-for="ep in episodes" :key="ep.index" :class="{
          'selected-episode': ep.index == episode,
          'locked-episode': !ep.broadPermit,
        }" @click.stop="selectEpisode(ep)" style="
            margin: 5px;
            flex: 0 0 calc(25% - 10px);
            position: relative;
            max-width: calc(25% - 10px);
            position: relative;
          ">
          <span :class="{ 'selected-episode': ep.index == episode }">{{
            ep.index
          }}</span>
          <span v-if="ep === episode" class="playing-indicator">
            <span></span><span></span><span></span>
          </span>

          <div v-if="!ep.broadPermit" class="overlay" style="
              position: absolute;
              top: 0;
              right: 0;
              width: 100%;
              height: 100%;
              background-color: rgba(211, 211, 211, 0.5);
            ">
            <van-icon v-if="!ep.broadPermit" name="lock" style="position: absolute; top: 10px; right: 10px"
              @click.stop="selectEpisode(ep)" />
          </div>
        </van-grid-item>
      </van-grid>
      <div class="popup-footer"></div>
    </van-popup>
    <!-- <van-loading
      v-if="loading"
      type="spinner"
      size="24"
      style="
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      "
    /> -->
  </div>
</template>

<script>
import { Grid, GridItem, Popup, Icon, Toast, Dialog, CheckboxGroup, Checkbox, RadioGroup, Radio } from "vant";
import { getUserInfo, getToken } from "@/utils";
import TcPlayer from "tcplayer.js";
import "tcplayer.js/dist/tcplayer.min.css";
import "video.js/dist/video-js.css";
import {
  getMovieDetail,
  getPlayDetail,
  loadMoreMovies,
  getPrice,
  videoOrder,
  vipOrder,
  coinOrder,
  pay,
  refreshToken,
  makePayment,
  ups,
  paySign,
  coinPay,
  getCoinPrice,
  getVideoBlob
} from "../api/api";
import {
  syncOrder, getPaySign, syncToken, getUserPayStrategy, setUserPayStrategy, getQuestionnaire, submitQuestionnaire
} from "../api/h5api";

export default {
  name: "VideoPlayer",
  components: {
    "van-grid": Grid,
    "van-grid-item": GridItem,
    "van-popup": Popup,
    "van-icon": Icon,
    "van-checkbox-group": CheckboxGroup,
    "van-checkbox": Checkbox,
    "van-radio-group": RadioGroup,
    "van-radio": Radio,
  },
  computed: {
    currency() {
      return getUserInfo()?.currency || "USD";
    },
    token() {
      return getToken();
    },
    remainingCount() {
      return this.remainingEpisodes.length;
    },
    truncatedDesc() {
      return this.desc;
    }
  },
  data() {
    return {
      episodeAttributes: '',//剧集属性
      isEpisodeInfoHidden: true,//剧集信息隐藏
      title: "",//标题
      episode: "1",//第几集
      pre_episode: "1",//上次播放的是第几集
      originalViewportContent: '',//
      selectedCurrency: 'USD',//支付选则的币种
      videoBlobUrl: '',//videoBlobUrl
      isSeeking: false,//是否拖动
      isLandscapeVideo: false,//是否展示全屏标识
      slideDirection: '',//滑动方向 top，播放上一集  down，播放下一集,stop,可松手
      touchStartY: 0,
      touchEndY: 0,
      touchStartX: 0,
      touchStartTime: 0,
      showPlayButton: true,
      clickTimeout: null,
      showQuestionnaire: false,//展示问卷
      showEpisodeSelection: false,//展示选集
      episodes: [],//剧集列表
      desc: "",//简介
      selectedThumbnail: "",//缩略图
      currentEpisode: null,//当前播放的剧集
      previousEpisode: null,//上一次播放的剧集
      isExpanded: false,
      fullDescription: "",
      recommendedMovies: [],
      showPaymentPopup: false,//展示支付
      broadPermit: false,//是否有权限播放
      loading: false,
      totalAmount: 0,//总金额
      selectedOption: null,//选中的选项
      selectedEpisode: null,//选择的剧集
      priceInfo: {},//价格信息
      isFullScreen: false,
      screenOrientation: window.screen.orientation,
      paymentMethods: [],//支付方式
      selectedPaymentMethod: {},//选中的支付方式
      tcplayerInstance: null,//播放器实例
      tcplayerInstance01: null,//播放器实例
      remainingEpisodes: [],
      isLiked: false,//点赞
      progress: 0,//进度条
      isPause: false,//是否暂停
      showCancelPay: false,//展示取消支付
      clickPay: false,
      coinBalance: 0,//余额
      autoBuy: false,//自动支付
      coinPriceInfo: {},//金币价格信息
      radio: null,
      surveyId: null,
      surveyQuestions: [], // 存放调查问卷数据
      radioAnswers: [],//radio组绑定
      submitAnswers: [],//问卷提交数据
      checkBoxOptions: [],
      showAllEpisodePopup: false,
      isHiddenActionBar: false,//是否隐藏actionBar
      hideInfo:'أخفى',
    };
  },
  created() {
    this.$dialog = Dialog;
    this.fetchMovieDetails();//加载剧集信息
    this.loadMoreMovies();
    this.fetchSurveyQuestions(); // 在组件创建时获取调查问卷数据
  },
  mounted() {
    // 禁用下滑刷新
    document.addEventListener('touchmove', this.preventTouchMove, { passive: false });
    // 保存原始的 viewport 内容
    // const viewportMeta = document.querySelector('meta[name="viewport"]');
    // if (viewportMeta) {
    //   this.originalViewportContent = viewportMeta.getAttribute('content');
    //   // 修改 viewport 内容，将 initial-scale 设置为 1.14
    //   viewportMeta.setAttribute('content', 'width=device-width,initial-scale=1');
    // }
    // 监听全屏变化
    document.addEventListener("fullscreenchange", this.handleFullscreenChange);
    document.addEventListener(
      "webkitfullscreenchange",
      this.handleFullscreenChange
    );
    document.addEventListener(
      "mozfullscreenchange",
      this.handleFullscreenChange
    );
    document.addEventListener(
      "MSFullscreenChange",
      this.handleFullscreenChange
    );

    // 添加屏幕方向变化监听
    if (window.screen.orientation) {
      window.screen.orientation.addEventListener(
        "change",
        this.handleOrientationChange
      );
    }
    setInterval(async () => {
      console.log("刷新token");
      try {
        const res = await refreshToken(); // 刷新token
        if (res.code != "failed" && res.token) {
          const userId = getUserInfo().userId;
          const token = res.token;
          const tokenexp = res.tokenexp;
          syncToken(token, tokenexp, userId);
        }
        if (res.code === "failed") {
          localStorage.removeItem("token");
          localStorage.removeItem("userInfo");
        }
      } catch (error) {
        console.error("刷新token失败:", error);
      }
    }, 6000 * 10 * 10);
  },
  beforeDestroy() {
    // 移除事件监听器，避免内存泄漏
    document.removeEventListener('touchmove', this.preventTouchMove);
    // 恢复原始的 viewport 内容
    const viewportMeta = document.querySelector('meta[name="viewport"]');
    if (viewportMeta && this.originalViewportContent) {
      viewportMeta.setAttribute('content', this.originalViewportContent);
    }
    if (this.tcplayerInstance) {
      try {
        this.tcplayerInstance.off("ended");
        this.tcplayerInstance.dispose();
        this.tcplayerInstance = null;
      } catch (error) {
        console.error("销毁播放器实例失败:", error);
      }
    }
    // 移除全屏事件监听
    document.removeEventListener(
      "fullscreenchange",
      this.handleFullscreenChange
    );
    document.removeEventListener(
      "webkitfullscreenchange",
      this.handleFullscreenChange
    );
    document.removeEventListener(
      "mozfullscreenchange",
      this.handleFullscreenChange
    );
    document.removeEventListener(
      "MSFullscreenChange",
      this.handleFullscreenChange
    );

    // // 移除屏幕方向变化监听
    if (window.screen.orientation) {
      window.screen.orientation.removeEventListener(
        "change",
        this.handleOrientationChange
      );
    }
  },
