    handleProgressTouchStart(event) {
      this.isSeeking = true;
      this.updateProgressFromTouch(event);
    },
    handleProgressTouchMove(event) {
      if (this.isSeeking) {
        this.updateProgressFromTouch(event);
      }
    },
    handleProgressTouchEnd(event) {
      this.isSeeking = false;
      // 调整视频播放位置
      const progressBar = document.getElementById("progress-bar");
      const rect = progressBar.getBoundingClientRect();
      const rectleft = rect.left;
      const clientX = event.changedTouches[0].clientX;
      const offsetX = clientX - rectleft;
      const width = rect.width;
      const duration = this.tcplayerInstance.duration();
      const seekTime = (offsetX / width) * duration;
      this.tcplayerInstance.currentTime(seekTime);
      this.updateProgress();
    },
    updateProgressFromTouch(event) {
      const progressBar = this.$el.querySelector('#progress-bar');
      if (progressBar) {
        const rect = progressBar.getBoundingClientRect();
        const touchX = event.touches[0].clientX;
        const progress = ((touchX - rect.left) / rect.width) * 100;
        this.progress = Math.min(Math.max(progress, 0), 100);
      }
    },

