      event.stopPropagation();
      this.isExpanded = !this.isExpanded;
      // 确保选集弹窗不会被触发
    },
    handleClick() {
      if (this.clickTimeout) {
        clearTimeout(this.clickTimeout);
        this.clickTimeout = null;
      } else {
        this.clickTimeout = setTimeout(() => {
          this.clickTimeout = null;
        }, 300);
      }
    },
    selectPaymentMethod(method) {
      this.selectedPaymentMethod = method;
    },
    handleTouchStart(event) {
      this.touchStartX = event.touches[0].clientX;
      this.touchStartTime = event.timeStamp;
    },
    // 新增：垂直触摸起始处理
    handleVerticalTouchStart(event) {
      console.log("垂直触摸开始");
      this.touchStartY = event.touches[0].clientY;
      this.deltaY = 0;
    },
    // 新增：垂直触摸移动处理（实现拖拽动效）
    handleVerticalTouchMove(event) {
      const touchEndY = event.touches[0].clientY;
      this.deltaY = touchEndY - this.touchStartY;
      console.log(this.deltaY);
      if (30 < this.deltaY && this.deltaY < 250) {
        this.slideDirection = "top";
      } else if (-30 > this.deltaY && this.deltaY > -250) {
        this.slideDirection = "down";
      } else if (this.deltaY > 250) {
        this.slideDirection = "top-stop";
      } else if (this.deltaY < -250) {
        this.slideDirection = "down-stop";
      }
      console.log("总时长" + this.tcplayerInstance.duration());
      console.log("当前时间" + this.tcplayerInstance.currentTime());
      // 限制最大拖拽距离为100px
      // const translateY = Math.min(Math.max(this.deltaY, -100), 100);
      event.currentTarget.style.transform = `translateY(0px)`;
    },

    // 新增：垂直触摸结束处理（判断滑动方向）
    handleVerticalTouchEnd(event) {
      
      this.slideDirection = '';
      const threshold = 250; // 滑动阈值
      const videoContainer = event.currentTarget;
      videoContainer.style.transform = ''; // 恢复位置
      const target = event.target || event.srcElement;
      const actonButton = document.querySelector('.acton-button');
      const progressBar = document.getElementById('progress-bar');
      const hiddenBar = document.getElementById('hidden-bar');
      const changeEpisodeBbar = document.getElementById('change-episode-bar');
      const episodeFullScreen = document.getElementById('episode-fullScreen');

      const isActionBtn = actonButton?.contains(target);
      const isProgressBar = progressBar?.contains(target);
      const isHiddenBar = hiddenBar?.contains(target);
      const ischangeEpisodeBbar = changeEpisodeBbar?.contains(target);
      const isEpisodeFullScreen = episodeFullScreen?.contains(target);
      const canPause = isActionBtn || isProgressBar || ischangeEpisodeBbar;
      if (isActionBtn) {
        const likeButton = document.querySelector('.like-button:nth-child(1)');
        const surveyButton = document.querySelector('.like-button:nth-child(2)');
        const downloadButton = document.querySelector('.like-button:nth-child(3)');
        const shareButton = document.querySelector('.like-button:nth-child(4)');
        const specButton = document.querySelector('.like-button:nth-child(5)');
        const selectEsipodeButton = document.querySelector('.like-button:nth-child(6)');
        if (likeButton?.contains(target)) {
          console.log('点击了点赞按钮');
          this.likeVideo();
          return;
        } else if (surveyButton?.contains(target)) {
          console.log('点击了问卷按钮');
        } else if (downloadButton?.contains(target)) {
          console.log('点击了下载按钮');
          // 使用 vant 的 Dialog 组件显示确认对话框
          this.$dialog.confirm({
            title: "تنزيل التطبيق",
            message: 'المحتوى المميّز حصريًا على تطبيق "تلك الكتب"',
            confirmButtonText: "واصِل المشاهدة",
            showCancelButton: false, // 添加此属性隐藏取消按钮
          })
            .then(() => {
              const isAndroid = /android/i.test(navigator.userAgent);
              const isIOS =
                /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;

              if (isAndroid) {
                window.location.href =
                  "https://play.google.com/store/apps/details?id=com.daoke.ar";
              } else if (isIOS) {
                window.location.href =
                  "https://itunes.apple.com/cn/app/تلك-الكتب/id1238024695?l=zh&ls=1&mt=8";
              } else {
                window.location.href =
                  "https://play.google.com/store/apps/details?id=com.daoke.ar";
              }
            });
          console.log('点击了下载按钮');
          return;
        } else if (shareButton?.contains(target)) {
          const shareData = "تم نسخ رابط الصفحة الحالية، شاركه مع أصدقائك";
          const url = window.location.href;
          navigator.clipboard.writeText(url).then(() => {
            Dialog.alert({
              message: shareData,
              confirmButtonText: 'أكّد'
            });
          });
          return;
        } else if (specButton?.contains(target)) {
          console.log('点击了简介按钮');
          this.isEpisodeInfoHidden = !this.isEpisodeInfoHidden;
          return;
        } else if (selectEsipodeButton?.contains(target)) {
          console.log('点击了选集按钮');
          return;
        }
      } else if (isEpisodeFullScreen) {
        this.tcplayerInstance.requestFullscreen();
        return;
      } else if (ischangeEpisodeBbar) {
        console.log('点击了选集按钮');
        const buttonId = event.target.id;
        if (buttonId === 'previous-episode') {
          console.log('点击了上一集按钮');
          const currentIndex = this.episodes.findIndex(ep => ep.id === this.currentEpisode.id);
          if (currentIndex > 0) {
            this.selectEpisode(this.episodes[currentIndex - 1]);
          }
        } else if (buttonId === 'next-episode') {
          console.log('点击了下一集按钮');
          const currentIndex = this.episodes.findIndex(ep => ep.id === this.currentEpisode.id);
          if (currentIndex < this.episodes.length - 1) {
            this.selectEpisode(this.episodes[currentIndex + 1]);
          }
        }
        return;
      }else if(isHiddenBar){ 
        return;
      }

      if (!canPause && this.deltaY == 0) {
        if (this.isPause) {
          this.tcplayerInstance.play();
          this.isPause = false;
        } else {
          this.tcplayerInstance.pause();
          this.isPause = true;
        }
      }

      if (this.deltaY < -threshold) {
        // 向上滑动（下一集）
        const currentIndex = this.episodes.findIndex(ep => ep.id === this.currentEpisode.id);
        if (currentIndex < this.episodes.length - 1) {
          this.selectEpisode(this.episodes[currentIndex + 1]);
        }
      } else if (this.deltaY > threshold) {
        // 向下滑动（上一集）
        const currentIndex = this.episodes.findIndex(ep => ep.id === this.currentEpisode.id);
        if (currentIndex > 0) {
          this.selectEpisode(this.episodes[currentIndex - 1]);
        }
      }
    },
    handleTouchMove(event) {
      const touchEndX = event.touches[0].clientX;
      const deltaX = touchEndX - this.touchStartX;
      const video = this.$refs.videoPlayer;
      const duration = video.duration;
      const newTime =
        video.currentTime + (deltaX / window.innerWidth) * duration;
      video.currentTime = Math.min(Math.max(newTime, 0), duration);
    },
    handleTouchEnd(event) {
      const touchDuration = event.timeStamp - this.touchStartTime;
      if (touchDuration < 500) {
        return;
      }
    },
    async selectEpisode(ep) {
      this.selectedEpisode = ep;
      this.pre_episode = this.episode;
      this.episode = ep.index;
      this.broadPermit = ep.broadPermit;
      console.log("选择的集数:", ep.index);
      // 显示加载提示
      Toast.loading({
        message: "loading...",
        forbidClick: true,
      });

      try {
        //获取用户自动付款策略
        const payStrategyRespone = await getUserPayStrategy();
        const payStrategy = payStrategyRespone.data;
        // 先获取播放详情
        const playResponse = await getPlayDetail(ep.id, ep.currency);
        this.previousEpisode = this.currentEpisode;
        this.currentEpisode = playResponse.data;
        this.showLike();
        this.broadPermit = playResponse.data.broadPermit;
        // 检查是否需要跳转到应用下载
        const locationApp = this.checkForAppDownload();
        if (locationApp) {
          return;
        }
        // 其余代码保持不变...
        if (!this.broadPermit) {
          if (document.fullscreenElement) {
            document.exitFullscreen();
          }
          const priceResponse = await getPrice(this.currentEpisode.id);
          // 新增回切第一集逻辑
          this.priceInfo = {
            data: priceResponse.data,
            vips: priceResponse.vips,
          };
          this.selectedOption = priceResponse.data;
          this.totalAmount = priceResponse.data?.price;
          this.$set(this.priceInfo);
        }
        this.remainingEpisodes = this.episodes.filter(
          (ep) => ep.broadPermit == false
        );
        if (this.currentEpisode?.viedoUrl_H) {
          const blobUrl = await getVideoBlob(this.currentEpisode.viedoUrl_H);
          // 释放视频资源
          URL.revokeObjectURL(this.videoBlobUrl);
          this.videoBlobUrl = blobUrl;
        }

        // 更新播放源
        if (this.tcplayerInstance) {
          try {
            // 先暂停
            this.tcplayerInstance.pause();
            // 更新源
            this.tcplayerInstance.src({
              src: this.videoBlobUrl,
              type: this.getVideoType(this.currentEpisode.viedoUrl_H),
            });
            // 适当延迟后播放，避免同时操作引起冲突
            setTimeout(() => {
              if (this.tcplayerInstance) {
                this.isPause = false;
                this.tcplayerInstance.play();
              }
            }, 300);
          } catch (error) {
            console.error("更新播放源失败:", error);
            // 如果更新源失败，尝试重新初始化播放器
            this.initTcplayer();
          }
        } else {
          // 如果播放器实例不存在，重新初始化
          this.initTcplayer();
        }
        if (!this.broadPermit) {
          // 关闭选择弹窗
          setTimeout(() => {
            //无权限解锁、设置为不自动支付、余额不足
            if ((!this.broadPermit && !payStrategy)) {
              this.showPaymentPopup = true;
            } else {
              //直接执行金币支付
              this.buyOneEpisode();
            }
            // 清除加载提示
            Toast.clear();
          }, 200);
        }
      } catch (error) {
        console.error("选择剧集失败:", error);
        Toast.clear();
      }
    },
    fetchMovieDetails() {
      const movieId = this.$route.query.id;
      const episodeId = this.$route.query.episodeId;
      if (movieId) {
        this.getMovieDetail(movieId, episodeId);
      } else {
        console.error("未找到电影ID");
      }
    },
    async getMovieDetail(movieId, episodeId) {
      try {
        const response = await getMovieDetail(movieId);
        console.log("response====?", response);
        this.title = response.data.title;
        this.episodes = response.data.chapters;
        this.desc = response.data.desc;
        this.fullDescription = response.data.fullDesc;
        this.selectedThumbnail = response.data.cover;
        if (episodeId) {
          //路由包含剧集id，则从该集开始播放
          if (this.episodes.length > 0) {
            let episodeIndex = 1;
            for (let index = 0; index < this.episodes.length; index++) {
              if (this.episodes[index].id == episodeId) {
                episodeIndex = this.episodes[index].index;
                break;
              }
            }
            console.log("episodeId", false);
            console.log("episodeIndex", episodeIndex);
            this.selectEpisode(this.episodes[episodeIndex - 1]);
          }
        } else {
          console.log("episodeId", true);
          // 路由不包含剧集id，则从第一集开始播放
          // 先获取播放详情再初始化播放器
          if (this.episodes.length > 0) {
            const firstEpisode = this.episodes[0];
            const playResponse = await getPlayDetail(firstEpisode.id);
            this.previousEpisode = this.currentEpisode;
            this.currentEpisode = playResponse.data;

            // 确保有视频地址后再初始化播放器
            if (this.currentEpisode?.viedoUrl_H) {
              const blobUrl = await getVideoBlob(this.currentEpisode.viedoUrl_H);
              console.log("blobUrl  :", blobUrl);
              this.videoBlobUrl = blobUrl;
              if (!this.tcplayerInstance) {
                this.initTcplayer();
              } else {
