          console.log("payResponse", payResponse);
          window.location.href = payResponse;
        }
      } else if (this.selectedOption.payType === "coin") {
        // 金币充值逻辑保持不变
        const coinOrderResponse = await coinOrder(
          this.selectedOption.code,
          this.selectedEpisode.id
        );
        console.log("金币订单结果:", coinOrderResponse);
        // 如果当前用户选择的是Payssion支付的话则拉起Payssion支付，否则仍使用Payment支付
        if (this.selectedPaymentMethod.id == 'mada_sa' || this.selectedPaymentMethod.id == 'stcpay_sa' || this.selectedPaymentMethod.id == 'applepay_sa') {
          //同步订单给网站后台
          syncOrder(coinOrderResponse.data.oId, getUserInfo()?.userId || '', this.totalAmount.toFixed(2), this.selectedCurrency, 'payssion', 'pending', 'coin', this.selectedPaymentMethod.id);
          // 触发Payssion表单提交
          if (this.$refs.payssionForm) {
            //支付方式
            const pmIdInput = this.$refs.payssionForm?.querySelector('#pm_id');
            if (pmIdInput) {
              pmIdInput.value = this.selectedPaymentMethod.id;
            }
            //金额
            const amountInput = this.$refs.payssionForm?.querySelector('#amount');
            if (amountInput) {
              amountInput.value = this.totalAmount.toFixed(2);
            }
            //币种
            const currencyInput = this.$refs.payssionForm?.querySelector('#currency');
            if (currencyInput) {
              currencyInput.value = this.selectedCurrency;
            }
            //订单号
            const orderIdInput = this.$refs.payssionForm?.querySelector('#order_id');
            if (orderIdInput) {
              orderIdInput.value = coinOrderResponse.data.oId;
            }
            //订单号
            const descriptionInput = this.$refs.payssionForm?.querySelector('#description');
            if (descriptionInput) {
              descriptionInput.value = '为订单' + coinOrderResponse.data.oId + '支付';
            }
            //returnUrl
            const returnUrlInput = this.$refs.payssionForm?.querySelector('#return_url');
            if (returnUrlInput) {
              returnUrlInput.value = window.location.href;
            }
            //api_key
            const apiKeyInput = this.$refs.payssionForm?.querySelector('#api_key');
            if (apiKeyInput) {
              const api_key = process.env.VUE_APP_PAY_API_KEY;
              apiKeyInput.value = api_key;
            }
            //api_key
            const apiSigInput = this.$refs.payssionForm?.querySelector('#api_sig');
            if (apiSigInput) {
              const api_key = process.env.VUE_APP_PAY_API_KEY;
              const secret_key = process.env.VUE_APP_PAY_Secret_Key;
              const api_sig = await paySign(api_key, this.selectedPaymentMethod.id, this.totalAmount.toFixed(2), this.selectedCurrency, coinOrderResponse.data.oId, secret_key);
              apiSigInput.value = api_sig;
            }
            this.$refs.payssionForm.submit();
          }
        } else {
          // 触发Payment支付
          const payResponse = await pay(
            coinOrderResponse.data.oId,
            this.selectedPaymentMethod.id,
