<template>
  <div class="douyin-style-container" @touchstart="handleTouchStart" @touchmove="handleTouchMove" @touchend="handleTouchEnd">
    <div class="video-wrapper" :style="{ transform: `translateY(${translateY}px)` }">
      <div v-for="(video, index) in videoList" :key="index" class="video-item">
        <video ref="videoPlayer" controls :src="video.url" class="video-element">
          您的浏览器不支持视频播放。
        </video>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      videoList: [
        { url: 'your_video_url_1' },
        { url: 'your_video_url_2' },
        // 可以添加更多视频
      ],
      startY: 0,
      currentIndex: 0,
      translateY: 0,
      threshold: 100 // 滑动阈值，超过该值则切换视频
    };
  },
  methods: {
    handleTouchStart(event) {
      this.startY = event.touches[0].clientY;
    },
    handleTouchMove(event) {
      const currentY = event.touches[0].clientY;
      this.translateY = currentY - this.startY;
    },
    handleTouchEnd() {
      if (this.translateY > this.threshold) {
        // 向下滑动，切换到上一集
        if (this.currentIndex > 0) {
          this.currentIndex--;
        }
      } else if (this.translateY < -this.threshold) {
        // 向上滑动，切换到下一集
        if (this.currentIndex < this.videoList.length - 1) {
          this.currentIndex++;
        }
      }
      this.translateY = 0;
    }
  }
};
</script>

<style scoped>
.douyin-style-container {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
  background-color: #000;
}

.video-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: transform 0.3s ease;
}

.video-item {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.video-element {
  max-width: 100%;
  max-height: 100%;
}
</style>