import axios from "axios";
import {getUserInfo,getTouristId } from "@/utils";
const albURL = process.env.VUE_APP_ALB_API 
// const albApi = axios.create({ albURL });

export const getRecommendedImg = async () => {
	try {
		const response = await axios.get(albURL+"/api/getRecommendedImg");
		return response.data;
	} catch (error) {
		console.error("خطأ:", error);
        return [];
		// throw error;
	}
};

//同步用户信息
export const syncUser = async (userId) => {
	try {
		const response = await axios.post(albURL+`/api/thirdpartyUser/syncUserInfo`, {
			userId: userId
		});
		return response.data;
	} catch (error) {
		console.error("同步token时出错:", error);
		throw error;
	}
};

//登录同步token
export const syncToken = async (token,tokenexp,userId) => {
	try {
		const response = await axios.post(albURL+`/api/thirdpartyUser/syncUserToken`, {
			userToken: token,
			expireTime: tokenexp,
			userId: userId
		});
		return response.data;
	} catch (error) {
		console.error("同步token时出错:", error);
		throw error;
	}
};

//同步订单
export const syncOrder = async (oId,userId,amount,currency,paymentMethod,status,payType,payChannel) => {
	try {
		const response = await axios.post(albURL+`/api/order/createOrder`, {
			orderId: oId,
			userId: userId,
			amount: amount,
			currency: currency,
			paymentMethod: paymentMethod, 
			status: status, 
			payType: payType, 
			payChannel: payChannel 
		});
		return response.data;
	} catch (error) {
		console.error("同步订单时出错:", error);
		throw error;
	}
};

//生成支付签名
export const getPaySign = async (oId,amount,currency,pm_id) => {
	try {
		const response = await axios.post(albURL+`/api/order/createTransactionSign`, {
			order_id: oId,
			amount: amount,
			currency: currency, 
			pm_id: pm_id, 
		});
		return response.data;
	} catch (error) {
		console.error("同步订单时出错:", error);
		throw error;
	}
};

// 调用后端接口记录用户行为
export const recordUserBehavior = async (click_url,code,remark) => {
	if(getUserInfo() || getTouristId()){
		const user_id = getUserInfo()?.userId || getTouristId();
		try {
			// 替换为实际的后端接口地址
			await axios.post(albURL+'/api/userBehavior/addUserBehavior', {
				// click_id: ,
				userId: user_id,
				clickUrl: click_url,
				code: code?code:'',
				remark:remark
			});
		} catch (error) {
			console.error('记录页面访问失败:', error);
		}
	}
};

// 调用记录用户支付设置
export const setUserPayStrategy= async (state) => {

	if(getUserInfo() || getTouristId()){
		const user_id = getUserInfo()?.userId || getTouristId();

		try {
			// 替换为实际的后端接口地址
			await axios.post(albURL+'/api/thirdpartyUser/updateAutoPayStatus', {
				// click_id: ,
				userId: user_id,
				autoPay: state
			});
		} catch (error) {
			console.error('记录用户支付设置失败:', error);
		}
	}
};

// 查询用户支付设置
export const getUserPayStrategy= async () => {

	if(getUserInfo() || getTouristId()){
		const user_id = getUserInfo()?.userId || getTouristId();
		try {
			// 替换为实际的后端接口地址
			const response = await axios.post(albURL+'/api/thirdpartyUser/queryAutoPayStatus', {
				userId: user_id
			});
			return response.data;
		} catch (error) {
			console.error('记录用户支付设置失败:', error);
		}
	}
};

// 获取问卷
export const getQuestionnaire= async () => {
	if(getUserInfo() || getTouristId()){
		try {
			// 替换为实际的后端接口地址
			const response = await axios.get(albURL+'/api/surveyQuestion/queryQuestionsBySurveyId');
			return response.data;
		} catch (error) {
			console.error('获取问卷失败:', error);
		}
	}
};

// 用户提交问卷
export const submitQuestionnaire= async (surveyId,answerDetailList) => {
	if(getUserInfo() || getTouristId()){
		const user_id = getUserInfo()?.userId || getTouristId();
		try {
			// 替换为实际的后端接口地址
			const response = await axios.post(albURL+'/api/surveySubmit/submit', {
				userId: user_id,
				surveyId: surveyId,
				answerDetailList: answerDetailList
			});
			return response.data;
		} catch (error) {
			console.error('提交问卷失败:', error);
		}
	}
};

