export const paySign =(api_Key,pm_id,amount,currency,order_id,secret_key) => {
	try {
		const originalData = api_Key + '|' + pm_id+'|'+amount+'|'+currency+'|'+order_id+'|'+secret_key;
		console.log(originalData);
		const md5Hash = CryptoJS.MD5(originalData).toString(CryptoJS.enc.Hex);
		return md5Hash;
	} catch (error) {
		console.error("创建支付签名出错:", error);
		throw error; // 抛出错误以便调用者处理
	}
  };

